// 
// components/hero
// ===============
// 

.hero{
  margin-top: $content-space-80;
  max-width: pxToRem(600);
  margin-left: auto;
  margin-right: auto;
  position: relative;
  z-index: 2;
  text-align: center;

  &__image svg{
    display: block;
    min-height: 200px;
    max-height: 260px;
    height: 240px;
    height: calc(40vh);
    margin: 0 auto;
  }
  &__content{
    margin-top: $content-space-32;
  }
  &__title{
    font-size: $font-size-5xl;
    line-height: $line-height-5xl;
    font-weight: bold;
    color: $color-dark;
    letter-spacing: 0;
  }
  &__copy{
    margin-top: $content-space-24;
    font-size: $font-size-base;
    line-height: $line-height-base;
  }
  &__cta-btn{
    margin-top: $content-space-24;
  }

  &__content--no-image{
    margin-top: $content-space-0;
  }
}

@include bp($md){
  .hero{
    margin-top: $content-space-100;
    max-width: none;
    margin-left: 0;
    margin-right: 0;
    display:flex;
    flex-direction: row-reverse;
    justify-content: space-between;
    align-items: flex-start;
    text-align: left;

    &__content{
      margin-top: $content-space-0;
      width: 50%;
    }
    &__image{
      width: 45.5%;
      max-width: 425px;
      margin-top: 00px;
      transform: translatex(5%);
    }
    &__image svg{
      width: 100%;
      height: auto;
      min-height: auto;
      max-height: none;
      margin: 0;
    }
    
    &--no-image{
      max-width: pxToRem(600);
      margin-top: $content-space-100;
      margin-left: auto;
      margin-right: auto;
      text-align: center;
    }
    &__content--no-image{
      width: auto;
      margin-top: $content-space-0;
    }
    &__title--no-image{
      font-size: $font-size-6xl;
      line-height: $line-height-6xl;
    }

  }

}

@include bp($lg){
  .hero{
    align-items: flex-start;
    
    &__image{
      width: 47.5%;
      max-width: none;
      margin-top: 0;
    }
    &__content{
      margin-top: calc(calc(100% - 824px)/4);
      min-width: pxToRem(490);
    }
    &__title{
      font-size: $font-size-7xl;
      line-height: $line-height-7xl;
    }
    &__copy{
      font-size: $font-size-lg;
      line-height: $line-height-lg;
    }
    &__cta-btn{
      margin-top: $content-space-32;
    }
    
    &--no-image{
      max-width: pxToRem(640);
      margin-top: $content-space-100;
    }
    &__content--no-image{
      margin-top: $content-space-0;
      min-width: auto;
    }
    &__title--no-image{
      font-size: $font-size-6xl;
      line-height: $line-height-6xl;
    }
    // &__copy--no-image{
    //   font-size: $font-size-lg;
    //   line-height: $line-height-lg;
    // }

  }
}

@include bp($xl){
  .hero{
    &__content{
      margin-top: $content-space-72;
    }

    &--no-image{
      max-width: 50%;
      margin-right: auto;
      margin-left: 0;
      margin-top: $content-space-150;
    }
    &__content--no-image{
      margin-top: $content-space-0;
      text-align: left;
    }
    &__title--no-image{
      font-size: $font-size-7xl;
      line-height: $line-height-7xl;
    }
  }
}