// 
// base/global
// ===========
// 

body{
    background-color: $color-light;
}

button {
    display: inline-block;
    border: none;
    background:none;
    padding: 0;
    margin: 0;
    text-decoration: none;
    cursor: pointer;
    text-align: center;
    outline: none;
    -webkit-appearance: none;
    -moz-appearance: none;
    font-family: inherit;
    font-size: inherit;
}

.wrapper{
    overflow: hidden;
    
    &--signup{
        @include bp($xl){
            min-height: 100vh;
            position: relative;
            &::before{
                content: "";
                top: 0;
                bottom: 0;
                left: 50%;
                transform: translateX(320px);
                right: 0;
                position: absolute;
                z-index: 0;
                background: no-repeat url(../assets/sign-up/bg-pattern-side.svg) left -780px top -800px / 2090px;
                background-color: $color-dark;
            }
        }
    }
}
